import axios from 'axios';

import dayjs from 'dayjs';

import {
    Space,
    SpinLoading,
    List,
    Image,
    Tag,
    Result,
    Toast,
    ImageViewer,
    Form,
    Input,
    Button,
    CapsuleTabs
} from 'antd-mobile';

import { SmileOutline, RedoOutline, MailOpenOutline } from 'antd-mobile-icons';

import localforage from 'localforage';

import styles from './index.module.css';

import { useEffect, useState } from 'react';
import { saveUuid, setPageInfo, getPageInfo } from '@/utils';

import { imagelist } from './image';

// const prefix = location.href.indexOf('localhost') !== -1 ? '' : 'https://api.jixiang.chat';
const prefix = 'https://api.jixiang.chat';


const copyToClip = (url?: string) => {
  if ( !url ) {
    return false;
  }
  // 
  const share = document.createElement('input');
  share.setAttribute('value', url || '');
  document.body.appendChild(share);
  share.select();
  document.execCommand('copy');
  document.body.removeChild(share);
  return true;
};

function sleep(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}

const timeMap = {
  '1H': '1K',
  '30m': '30K'
};


const demoImages = [
  'https://gw.alicdn.com/imgextra/i4/O1CN01AVuksB1Iz6H9dux5y_!!6000000000963-0-tps-592-1280.jpg',
  'https://gw.alicdn.com/imgextra/i4/O1CN01m3GgVl1aa3hiq3Gei_!!6000000003345-0-tps-592-1280.jpg'
];

const ALGO_INTRO = {
    'dea-cross-upper-diff-long': 'MACD DEA',
    'dea-cross-upper-diff-short': 'MACD DEA',
    'dea-cross-lower-diff-long': 'MACD DEA',
    'dea-cross-lower-diff-short': 'MACD DEA',
    'macd-sticks-1-beili': 'MACD',
    'macd-sticks-2-beili': 'MACD',
    'macd-sticks-3-beili': 'MACD',
    'macd-cross-diff-long': '12EMA, ',
    'macd-cross-diff-short': '12EMA,',
    'td13': '1313',
    'td9': '99',
    'boll': '',
    'DEA': 'DEA',
    'trend_reverse_short': 'reverse_short',
    'trend_reverse_long': 'reverse_long'
  };


let clickIns = 0;
let payClickIns = 0;

let PAY_COUNTS = 0;

const AlgoList = (props)=>{


    const today = dayjs();
    const now_today = today.format('YYYY/MM/DD');
    
    const [count, setCount] = useState(0);

    const [paycount, setPayCount] = useState(0);

    const [show, setShow] = useState(false);

    const [payLoading, setPayLoading] = useState(false);

    const [form] = Form.useForm();

    const [app, setApp] = useState({
        isLoading: true,
        loaded: false,
        time: '',
        data: [],
        needPay: false,
    });

    const {
      info
    } = props;



    const initPayCounts = async ()=>{
      if ( !getPageInfo('left_usdt') ) {
        setPageInfo('left_usdt', 1);
        if ( info.left_usdt !== undefined ) {
          setPayCount(info.left_usdt || 0);
          PAY_COUNTS = info.left_usdt || 0;
          await localforage.setItem('pay_counts', JSON.stringify({
            count: PAY_COUNTS
          }));
        }
      } else {
        const onedatas: any = await localforage.getItem('pay_counts');
        if ( onedatas ) {
          const datas = JSON.parse(onedatas);
          setPayCount(datas.count || 0);
          PAY_COUNTS = datas.count || 0;
        } 
      }
    };

    const initCounts = async ()=>{
      const onedatas: any = await localforage.getItem('rtime_counts');

      const initDefaultCounts = async (refresh: boolean)=>{
        let default_counts = refresh ? 11 : (info.today_usdt || 0);

        if ( info.gmt_update ) {
          await saveUuid({
            gmt_update: 'null'
          });
        }

        if ( default_counts <= 0 && PAY_COUNTS <= 0 ) {
          setApp({
            isLoading: false,
            loaded: true,
            time: '',
            data: [],
            needPay: true
           });
          return;
        }

        await localforage.setItem('rtime_counts', JSON.stringify({
          count: default_counts,
          date: now_today
        }));
        if ( refresh ) {
          await saveUuid({
            today_usdt: default_counts
          });
        }
        setCount(default_counts);
        fetchData(false, default_counts);
      }

      if ( onedatas ) {
        const datas = JSON.parse(onedatas);

        if ( datas.date !== now_today ) {
          await initDefaultCounts(true);
        } else if ( info.gmt_update ) {
          await initDefaultCounts(false);
        } else {
          if ( !getPageInfo('today_usdt') ) {
            setPageInfo('today_usdt', 1);
            setCount(info.today_usdt || datas.count);
            fetchData(false, info.today_usdt || datas.count);
          } else {
            setCount(datas.count);
            fetchData(false, datas.count);
          }
        }
      } else {
        await initDefaultCounts(false);
      }
    };

    const fetchData = async (flag?: boolean, counts?: number)=>{

        const adatas: any = await localforage.getItem('rtime');

        const dconfirm = counts !== undefined ? counts === 0 && PAY_COUNTS === 0 : count === 0 && paycount === 0;

        if ( dconfirm ) {
          setApp({
            isLoading: false,
            loaded: true,
            time: '',
            data: [],
            needPay: true
           });
          return;
        }

        if ( flag ) {
          setApp({
            isLoading: true,
            loaded: false,
            time: '',
            data: [],
            needPay: false
          });
        }

        if ( !flag && adatas ) {
           try{
             const datas = JSON.parse(adatas);
             const beforeTime = new Date(datas.time).getTime();
             const nowTime = new Date().getTime();
             const diffTime = nowTime - beforeTime;
             const compareTime = 30*60*1000;
            //  const compareTime = 10;
             if ( diffTime < compareTime ) {
               setApp({
                isLoading: false,
                loaded: true,
                time: datas.time,
                data: datas.onelist.concat(datas.twolist),
                needPay: false
               });
               return;
             }

           }catch(e){}
        }

        const res = await axios(`https://newdemo.jixiang.chat/proxyhttp?apitype=recent-algo&apitag=CHATGPT`, {
            method: 'get'
        });

        // tis

        if ( res?.data?.success ) {
           const pdatas = res?.data?.data;
           await localforage.setItem('rtime', JSON.stringify(pdatas));
           setApp({
            isLoading: false,
            loaded: true,
            time: pdatas.time,
            data: pdatas.xlist,
            needPay: false
           });
           if ( counts !== undefined && counts !== 0 || count !== 0 ) {
             const left_counts = counts !== undefined ? (counts - 1) : (count - 1);
             await localforage.setItem('rtime_counts', JSON.stringify({
               date: now_today,
               count: left_counts
             }));
             await saveUuid({
               today_usdt: left_counts
             });
             setCount(left_counts);
           } else if ( PAY_COUNTS !== -1 && PAY_COUNTS > 0 || paycount !== 0 ) {
             const left_pay_counts = PAY_COUNTS !== -1 ? PAY_COUNTS - 1 : paycount - 1;
             await localforage.setItem('pay_counts', JSON.stringify({
              count: left_pay_counts
             }));
             await saveUuid({
              left_usdt: left_pay_counts
             });
             PAY_COUNTS = -1;
             setPayCount(left_pay_counts);
           }
           
        } else {
          setApp({
            isLoading: false,
            loaded: true,
            time: '',
            data: [],
            needPay: false
           });
        }
    }

    const checkErros = async (flag?: boolean)=>{

      let errTititle = '';
      try{
        await form.validateFields();
      }catch(e) {
        if ( e?.errorFields.length ) {
          for ( let i = 0, len = e?.errorFields.length; i < len ; i ++ ) {
            const target = e?.errorFields[i];
            if ( target.errors.length ) {
              errTititle = target.errors[0];
              break;
            }
          }
        }
      }
      
      if ( flag && errTititle ) {
        Toast.show({
          icon: 'fail',
          content: errTititle,
        });
      }
      return errTititle;
    }

    const onPay = async ()=>{

      if ( payClickIns === 0 ) {
          payClickIns = new Date().getTime();
      } else {
          const now = new Date().getTime();
          const dif = now - payClickIns;
          if ( dif < 10*1000 ) {
            Toast.show({
              icon: 'fail',
              content: `, ${Math.ceil(dif/1000)}S`
            });
            return;
          }
      }

      setPayLoading(true);
      const error = await checkErros(true);
      if ( error ) {
        setPayLoading(false);
        return;
      }
      const values = form.getFieldsValue();
      const res = await axios(`${prefix}/api/btc/list?apitype=getDepositHistory&apitag=CHATGPT`, {
          method: 'get',
          params: {
            from: values.account,
            removeOld: true
          },
      });

      if ( res?.data?.success && res?.data?.data?.length ) {

        let usdts = 0;
        let caches_hash = [];

        const dep_list = res?.data?.data || [];

        for ( let i = 0, len = dep_list.length; i < len; i ++ ) {
          usdts += (dep_list[i].amt - 0);
          caches_hash.push(
            `${dep_list[i].txId}`
          );
        }

        try{
          if ( caches_hash.length ) {
            await axios(`${prefix}/api/btc/list?apitype=saveDepositHash`, {
              method: 'get',
              params: {
                datas: caches_hash.join(',')
              },
            });
          }
        }catch(e){}
      
        let xcounts = Math.ceil(usdts/0.1);
        await localforage.setItem('pay_counts', JSON.stringify({
          caches_hash: caches_hash,
          count: xcounts
        }));
        await saveUuid({
          left_usdt: xcounts
        });
        PAY_COUNTS = xcounts;
        clickIns = 0;
        setPayCount(xcounts);
        props.onChange && props.onChange({
          left_usdt: xcounts,
          today_usdt: 0
        });
        setApp({
          ...app || {},
          needPay: false,
        });
      } else {
        Toast.show({
          icon: 'fail',
          content: `${values.account}`
        });
      }
      setPayLoading(false);
    };

    useEffect(()=>{
      const init = async ()=>{
        await initPayCounts();
        await initCounts();
      };
      init();
    }, []);


    if ( app.isLoading ) {
      return <>
                <div style={{marginTop: 140}} />
                <Space justify='center' block>
                <SpinLoading color='primary' />
                </Space>
            </>
    }


    const cons: any = [];

    if ( !app.needPay ) {
      cons.push(
        <div className={styles.wraptime}>
            {app.time}
            <span onClick={()=>{
              if ( clickIns === 0 ) {
                  clickIns = new Date().getTime();
                  fetchData(true);
              } else {
                  const now = new Date().getTime();
                  const dif = now - clickIns;
                  if ( dif < 900000 ) {
                    Toast.show({
                      icon: 'fail',
                      content: '5'
                    });
                  } else {
                    fetchData(true);
                  }
              }
            }}><RedoOutline style={{marginLeft: 20}}/><span style={{fontSize: 10}}></span></span>
        </div>
      );

      const tabList = [{
        title: '',
        taglist: []
      },{
        title: '',
        taglist: ['BTC-USDT-SWAP', 'ETH-USDT-SWAP', 'ADA-USDT-SWAP', 'SOL-USDT-SWAP']
      },
      // ,{
      //   title: '',
      //   taglist: ['reverse_short', 'reverse_start_short']
      // },{
      //   title: '',
      //   taglist: ['reverse_long', 'reverse_start_long']
      // },
      {
        title: '',
        taglist: ['long']
      },{
        title: '',
        taglist: ['short']
      }];

      let long = 0, short = 0;

      (app?.data||[]).forEach(item=>{
         if ( item.pos_side === 'long' ) {
           long ++;
         }
         if ( item.pos_side === 'short' ) {
           short ++;
         }
      });

      let xcon = [];

      if ( long > 0 || short > 0 ) {
         xcon.push(
            <p>
              <span style={{marginLeft: '20px'}}>{long}</span>
              <span style={{marginLeft: '10px'}}>{short}</span>
              <span style={{marginLeft: '20px'}}>{
                long > short ? '' : ''
              }</span>
            </p>
         );
      }

      let xindex = 0;
      const x_tabList = tabList.map((item, index)=>{
        const targetList = (app?.data||[]).filter(it=>{
          if ( item.title === '' ) {
            return true;
          }
          if ( item.title === ''&& item.taglist.indexOf(it.ins_id) !== -1 ) {
              return true;
          }
          if ( item.title === '' && (it.algo.indexOf(item.taglist[0]) !== -1 || it.algo.indexOf(item.taglist[1]) !== -1) ) {
            return true;
          }
          if ( item.title === '' && (it.algo.indexOf(item.taglist[0]) !== -1 || it.algo.indexOf(item.taglist[1]) !== -1) ) {
            return true;
          }
          if ( item.title === '' && it.pos_side === 'long' ) {
            return true;
          }
          if ( item.title === '' && it.pos_side === 'short' ) {
            return true;
          }
          return false;
        })
        item.list = targetList;
        if ( xindex === 0 && targetList.length ) {
           xindex = index;
        }
        return item;
      });

      console.log(x_tabList,'x_tabList')

      cons.push(
         <div 
            className={styles.wraplist}
            style={{
                // maxHeight: document.documentElement.clientHeight - 350,
                overflowY: 'auto',
                margin: '10px 20px'
            }}>
              {xcon}
              {
                !(app?.data||[]).length ? <Result
                icon={<SmileOutline />}
                status='success'
                title=''
                description=''
              /> : null
              }
              {
                (app?.data||[]).length ? 
              <CapsuleTabs defaultActiveKey={`${xindex}`}>
                {
                  x_tabList.map((item, index)=>{
                    return <CapsuleTabs.Tab title={item.title} key={index} destroyOnClose>
                              <List>
                                  {
                                    !item.list.length ? 
                                    <Result
                                        icon={<SmileOutline />}
                                        status='success'
                                        title=''
                                        description={<p></p>}
                                      />: null
                                  }
                                  {item.list.map((user: any, index) => (
                                      <List.Item
                                          key={index}
                                          prefix={
                                              <div className={styles.listicon}>
                                                  <Image
                                                      src={imagelist[user.ins_id]?.img}
                                                      style={{ borderRadius: 20 }}
                                                      fit='cover'
                                                      width={32}
                                                      height={32}
                                                  />
                                              </div>
                                          }
                                          description={
                                              <div className={styles.defs}>
                                              <p>
                                                  <label></label>
                                                  <Tag color={user.pos_side === 'long' ? 'danger' : '#87d068'} fill='outline'>
                                                    {user.pos_side === 'long' ? '' : ''}
                                                  </Tag>
                                              </p>
                                          </div>
                                          }
                                      >
                                          {user.ins_id.replace('-USDT-SWAP', '')}<span className={styles.desx}>( {imagelist[user.ins_id]?.alias} )</span>
                                      </List.Item>
                                  ))}
                              </List>
                          </CapsuleTabs.Tab>
                  })
                }
              </CapsuleTabs> : null
              }
            </div>
      );
      cons.push(
        <div className={styles.wrapdiscord}>
              <p style={{color: '#999'}}><b style={{fontSize: 14, margin: '0 5px'}}>{count > 0 ? count : 0}</b> </p>
              {
                paycount ?
                <p style={{color: '#999'}}><b style={{fontSize: 14, margin: '0 5px'}}>{paycount}</b> </p> : null
              }
              <p>Discord<a onClick={()=>{
                   ImageViewer.Multi.show({ images: demoImages })
              }}>()</a></p>
              <p><a href="https://discord.gg/8UPC9Hj5Mr" target="_blank">https://discord.gg/8UPC9Hj5Mr</a></p>
            </div>
      );
    } else {

      if ( !show ) {
        cons.push(
          <Result
            icon={<SmileOutline />}
            status='success'
            title=''
            description={<p>0.1U = 1<a onClick={()=>{setShow(true)}}></a></p>}
          />
        );
        cons.push(
          <div className={styles.wrapdiscord}>
            <p style={{color: '#999'}}><b style={{fontSize: 14, margin: '0 5px'}}>0</b> </p>
      
            <p>Discord<a onClick={()=>{
                  ImageViewer.Multi.show({ images: demoImages })
            }}>()</a></p>
            <p><a href="https://discord.gg/8UPC9Hj5Mr" target="_blank">https://discord.gg/8UPC9Hj5Mr</a></p>
          </div>
        );
      } else {
        cons.push(
          <div 
              className={styles.wraplidst}
              style={{
                  // maxHeight: document.documentElement.clientHeight - 300,
                  overflowY: 'auto',
                  margin: '10px 20px'
              }}>
            <Form
                name='form'
                form={form}
                  footer={
                  <Button 
                    onClick={onPay}
                    loading={payLoading}
                    block 
                    type='submit' 
                    color='primary' 
                    size='large'>
                      
                  </Button>
                  }>
              <Form.Item label=''>
                <p>1251223923@qq.com<span onClick={()=>{
                  if ( copyToClip('1251223923@qq.com') ) {
                    Toast.show({
                      icon: 'success',
                      content: ''
                    });
                  }
                }}><MailOpenOutline /></span></p>
              </Form.Item>
              <Form.Item name="account" label='OKX' rules={[{ required: true }]}>
                <Input placeholder='OKX' />
              </Form.Item>
            </Form>
          </div>
        )
        cons.push(
          <div className={styles.wrapdiscord}>
            <h5></h5>
            <p style={{lineHeight:1.8}}>0.1U = 110 ^_^</p>
            <ol className={styles.wrapol}>
              <li>OKXOKX</li>
              <li>OKX    USDT </li>
              <li>1251223923@qq.com  </li>
              <li>2 ~ 10</li>
            </ol>
            </div>
        );
      }
    }

    return <>
            {cons}
          </>
};


export default AlgoList;